"use client";

import { createProduct, deleteProduct, fetchProducts, updateProduct } from "@/redux-counter/curd/postthunk";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";


export default function home3() {
  const dispatch = useDispatch();
  const { products, loading, error } = useSelector((state) => state.product);
  const [newProduct, setNewProduct] = useState({ title: "", body: "" });

  useEffect(() => {
    dispatch(fetchProducts(data));
  }, [dispatch]);

  const handleCreate = () => {
    if (newProduct.title) {
      dispatch(createProduct(newProduct));
      setNewProduct({ title: "", body: "" });
    }
  };

  const handleUpdate = (id) => {
    const updatedData = { title: "Updated Title", body: "Updated Content" };
    dispatch(updateProduct({ id, data: updatedData }));
  };

  const handleDelete = (id) => {
    dispatch(deleteProduct(id));
  };

  return (
    <div className='min-h-screen bg-pink-400'>
      <h1>Product List</h1>
      <input
        type="text"
        placeholder="Title"
        value={newProduct.title}
        onChange={(e) => setNewProduct({ ...newProduct, title: e.target.value })}
      />
      <input
        type="text"
        placeholder="Body"
        value={newProduct.body}
        onChange={(e) => setNewProduct({ ...newProduct, body: e.target.value })}
      />
      <button onClick={handleCreate}>Add Product</button>

      {loading && <p>Loading...</p>}
      {error && <p>Error: {error}</p>}

      <ul>
        {products.map((product) => (
          <li key={product.id}>
            {product.title} - {product.body}
            <button onClick={() => handleUpdate(product.id)}>Update</button>
            <button onClick={() => handleDelete(product.id)}>Delete</button>
          </li>
        ))}
      </ul>
    </div>
  );
}